:host {
  display: block;
  width: 100%;
  position: relative;
  min-height: 100%;
  padding: 4px 0;
}

.c3-tree-loader {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

mat-tree {
  width: 100%;
  height: 100%;
  background-color: transparent;
  border: 1px solid var(--mat-divider-color);
  border-radius: 4px;
  overflow: auto;
}
mat-tree mat-tree-node {
  display: flex;
  align-items: center;
  justify-content: start;
  gap: 1rem;
}
mat-tree mat-tree-node.has-children > button {
  height: 32px;
  width: 32px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid transparent;
  margin: 0.5rem;
}
mat-tree mat-tree-node.has-children > button:hover {
  border-color: var(--mat-divider-color);
}
mat-tree mat-tree-node.has-children:hover {
  background-color: var(--background-hover);
}
mat-tree mat-tree-node.has-children .mat-tree-node-content {
  padding-right: 0.25rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
mat-tree mat-tree-node.children button {
  width: 100%;
  border-radius: 0.25rem;
  padding: 0 1rem;
  height: 2rem;
  text-align: left;
}
mat-tree mat-tree-node.children button:hover {
  background-color: var(--background-hover);
}
mat-tree mat-tree-node:hover {
  background-color: var(--background-hover);
}
